













































































































.form {
  &__wrapper {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    border-radius: 5px;
    padding: 30px;
    color: #9297a3;
    box-shadow: 0 0 10px 5px rgba(0, 0, 0, 0.25);
    min-width: 40vw;
    z-index: 30;
  }

  &__overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: rgba(49, 54, 63, 0.5);
    z-index: 25;
  }

  &__layout {
    display: flex;
    flex-direction: column;
  }

  &__header {
    color: #9297a3;
    font-size: 24px;
    margin: 0 0 20px 0;
  }

  &__button-wrapper {
    align-self: flex-start;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 12px;
  }

  &__label {
    font-size: 1rem;
    font-weight: normal;
    margin: 0 0 16px 0;
  }

  &__text,
  &__select {
    height: 30px;
    display: flex;
    align-items: center;
    padding: 0 8px;
    justify-content: flex-start;
    background: #e7e8ec;
    margin: 0 0 16px 0;
  }

  &__select {
    font-size: 1rem;
    font-family: Roboto, sans-serif;
    font-weight: normal;
    margin: 0 0 32px 0;
  }
}
