
































































.ticket {
  &__wrapper {
    background: #fff;
    color: #c5c5c5;
    margin: 0 0 8px 0;
    display: grid;
    height: 60px;
    grid-template-columns: 1fr minmax(0, 4fr) 6fr 3fr 3fr 3fr;
    grid-gap: 4px;
    align-items: center;
    padding: 5px 10px;
    font-size: 1rem;
    font-weight: 400;
  }

  &__number {
    background: #ff6c60;
    padding: 4px;
    border-radius: 2px;
    color: #fff;
    margin: auto;
    display: inline-block;
  }
}

.user {
  &__wrapper {
    display: flex;
    align-items: center;
  }

  &__photo {
    margin: 0 4px 0 0;
    &-img {
      width: 32px;
      height: 32px;
      object-fit: cover;
    }
  }

  &__name {
    color: #dcdcdc;
  }
}

.message {
  &__wrapper {
    background: #f6f6f6;
    border-left: 3px #dcdcdc solid;
    display: flex;
    flex-direction: column;
    padding: 4px;
    overflow: hidden;
  }

  &__header {
    font-size: 0.825rem;
    margin-bottom: 4px;
  }

  &__text {
    font-size: 0.825rem;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  &__status {
    position: relative;
    padding: 0 0 0 12px;
  }

  &__circle {
    content: "";
    top: 50%;
    left: 0px;
    width: 10px;
    height: 10px;
    border-radius: 5px;
    position: absolute;
    transform: translate(0, -50%);
    background: #ff6c60;
  }

  &__type {
    color: #ff6c60;
    font-weight: 600;
  }
}
